<template>
  <a-input v-model:value="url" placeholder="http://......" /><a-button
    type="primary"
    @click="submitURL"
    ></a-button
  >
  <a-button @click="update"></a-button>
  <a-table
    :columns="columns"
    :data-source="dataSource"
    :row-key="(record) => record.id"
  >
    <template #bodyCell="{ column, record }">
      <template v-if="column.key === 'img'"
        ><a-image :width="40" :src="record.img" />
      </template>
      <template v-if="column.key === 'name'">
        <a :href="record.url">{{ record.name }}</a>
      </template>
      <template v-else-if="column.key === 'available'">
        <div v-if="record.available"><check-outlined /></div>
        <div v-else><close-outlined /></div
      ></template>
      <template v-if="column.dataIndex === 'target_price'">
        <a-input
          style="width: 80px"
          v-model:value="record.target_price"
          @pressEnter="changeTarget(record.id, record.target_price)"
        />
      </template>
      <template v-else-if="column.key === 'updated_at'">{{
        record.updated_at.split(".")[0]
      }}</template>
      <template v-else-if="column.dataIndex === 'operation'">
        <a-popconfirm
          v-if="dataSource.length"
          title="Sure to delete?"
          @confirm="onDelete(record.id)"
        >
          <a>Delete</a>
        </a-popconfirm>
      </template>
    </template>
  </a-table>

  <a-modal
    v-model:visible="modalVisible"
    :title="formState.platform + ' ' + formState.name"
    @ok="addArticle"
  >
    <a-form
      ref="formRef"
      :model="formState"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-form-item
        label=""
        :rules="[{ required: true, message: 'Please input your !' }]"
      >
        <a-select v-model:value="formState.skuId" @change="skuChange">
          <a-select-option
            v-for="item in options"
            :value="item.skuId"
            :key="item.skuId"
            >{{ item.color }}___{{ item.size }}</a-select-option
          >
        </a-select>
      </a-form-item>
      <a-form-item
        label=""
        :rules="[{ required: true, message: 'Please input your targetprice!' }]"
      >
        <a-input-number v-model:value="formState.target_price" />
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script>
import { ref, defineComponent, onMounted, reactive, toRaw } from "vue";
import { CheckOutlined, CloseOutlined } from "@ant-design/icons-vue";
import { message } from "ant-design-vue";
import axios from "axios";

const columns = [
  {
    title: "",
    dataIndex: "img",
    key: "img",
  },
  {
    title: "",
    dataIndex: "platform",
    key: "platform",
  },
  {
    title: "",
    dataIndex: "name",
    key: "name",
  },
  {
    title: "",
    dataIndex: "color",
    key: "color",
  },
  {
    title: "",
    dataIndex: "size",
    key: "size",
  },
  {
    title: "",
    dataIndex: "available",
    key: "available",
    width: 60,
    align: "center",
  },
  {
    title: "",
    dataIndex: "latest_price",
    key: "latest_price",
    width: 90,
    align: "right",
  },
  {
    title: "",
    dataIndex: "target_price",
    key: "target_price",
  },
  {
    title: "",
    dataIndex: "updated_at",
    key: "updated_at",
  },
  {
    title: "",
    dataIndex: "operation",
  },
];
export default defineComponent({
  components: { CheckOutlined, CloseOutlined },
  setup() {
    const formRef = ref();
    const dataSource = ref([]);
    const modalVisible = ref(false);
    const url = ref();
    const formState = reactive({
      platform: "",
      img: "",
      color: "",
      size: "",
      url: "",
      name: "",
      skuId: "",
      target_price: "",
    });
    const options = ref([]);
    const baseUrl = "https://szhsite.herokuapp.com";
    
    //const baseUrl = "http://127.0.0.1:8000";
    //const baseUrl = "http://23.105.216.195:8000";

    const fetch = () => {
      axios
        .get(baseUrl + "/api/articles/")
        .then(function (response) {
          dataSource.value = response.data;
        })
        .catch(function (error) {
          message.error(error);
        });
    };
    function copyObj(obj1, obj2) {
      let key = "";
      for (key in obj1) {
        if (key in obj2) {
          obj2[key] = obj1[key];
        }
      }
    }
    const submitURL = () => {
      axios
        .get(baseUrl + "/api/getchoice/" + encodeURIComponent(url.value))
        .then(function (response) {
          copyObj(response.data, formState);
          options.value = response.data.skulist;
          modalVisible.value = true;
        })
        .catch(function (error) {
          console.log(error);
          message.error(error);
        });
    };
    const skuChange = () => {
      options.value.forEach(function (item) {
        if (item.skuId === formState.skuId) {
          formState.color = item.color;
          formState.size = item.size;
        }
      });
    };

    const addArticle = () => {
      axios
        .post(baseUrl + "/api/articles/", toRaw(formState))
        .then(function () {
          message.info("create success");
          fetch();
          modalVisible.value = false;
          url.value = "";
          for (let key in formState) {
            formState[key] = "";
          }
        })
        .catch(function (error) {
          message.error(error);
        });
    };
    const onDelete = (id) => {
      axios
        .delete(baseUrl + "/api/articles/" + id + "/")
        .then(function () {
          message.info("delete");
          fetch();
        })
        .catch(function (error) {
          message.error(error);
        });
    };

    const update = () => {
      axios
        .get(baseUrl + "/api/trigger")
        .then(function () {
          message.info("updating");
          setTimeout(function () {
            fetch();
            message.info("refreshed");
          }, 5000);
        })
        .catch(function (error) {
          message.error(error);
        });
    };

    const changeTarget = (id, target) => {
      axios
        .patch(baseUrl + "/api/articles/" + id + "/", {
          target_price: target,
        })
        .then(function () {
          message.info("target price changed");
        })
        .catch(function (error) {
          message.error(JSON.stringify(error));
          fetch();
        });
    };

    onMounted(() => {
      fetch();
    });

    return {
      formRef,
      url,
      columns,
      dataSource,
      modalVisible,
      formState,
      options,
      skuChange,
      copyObj,
      submitURL,
      addArticle,
      onDelete,
      update,
      changeTarget,
    };
  },
});
</script>


